body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: white;
  font-weight: 100;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: #00ffd2;
  font-weight: 500;
}


#root {
  background: #23a6d5; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #23a6d5 25%,#23a6d5 10%,#23c5d3 59%,#23d5ab 100%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(25%, #23a6d5),color-stop(10%, #23a6d5),color-stop(59%, #23c5d3),to(#23d5ab));
  background: -o-linear-gradient(top, #23a6d5 25%,#23a6d5 10%,#23c5d3 59%,#23d5ab 100%);
  background: linear-gradient(to bottom, #23a6d5 25%,#23a6d5 10%,#23c5d3 59%,#23d5ab 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#23a6d5', endColorstr='#23d5ab',GradientType=0 ); /* IE6-9 */
  overflow: hidden;
}

section {
  height: 100vh;
  width: 100%;
  position: relative;
}

.presentation {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
  display: -ms-flexbox;
  display: flex;
}

.description {
  height: calc(100vh - 630px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.job {
  position: relative;
  margin-top: 10px;
  padding-top: 2px;
  padding-left: 20px;
  margin-bottom: 20px;
}

.job > .picker {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
}

.links {
  position: relative;
  width: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: line;
      flex-direction: line;
  padding-top: 20px;
}

.links:before {
  content: '';
  position: absolute;
  width: 10px;
  height: 2px;
  background: white;
  top: 0;
  left: calc(50% - 5px);
}

.links > a {
  text-align: center;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}

.links > a > img {
  width: 30px;
  cursor: pointer;
}

.description > span > strong {
  font-size: 80px;
}

.portrait {
  position: absolute;
  width: 840px;
  height: 630px;
  left: calc(50% - 420px);
  bottom: 0;
  overflow: hidden;
}

.portrait > img {
  position: absolute;
  width: 100%;
  opacity: 0.2;
}

@media (max-width: 768px){
  .description {
    height: calc(100vh - 450px);
  }

  .portrait {
    width: 600px;
    height: 450px;
    left: calc(50% - 300px);
    bottom: 0 !important;
  }
}

@media (max-width: 420px){
  .description {
    height: calc(100vh - 300px);
  }

  .portrait {
    width: 400px;
    height: 300px;
    left: calc(50% - 200px);
  }
}



